*,
*::before,
*::after{
    box-sizing: border-box;
}

.App{
    background-color: var(--dark);
    color: var(--clr-light);
    margin: 0;
    font-family: var(--ff-primary);
    font-size: var(--fs-body);
    background-image: url("./assets/img/poly2.svg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    overflow-x: hidden;
}
:root{
    --ff-primary : 'Prompt', sans-serif;
    --ff-secondary : 'Source Code Pro', monospace;
    --fw-reg: 300;
    --fw-bold: 900;


    --clr-light : #f5f5f5;
    --clr-dark: #303030;
    --clr-accent: #16e0bd;
    --glass-bg : rgba(28, 28, 38, .4);

    --fs-h1: 3rem;
    --fs-h2: 2.25rem;
    --fs-h3: 1.25rem;
    --fs-body:1rem;

    --bs : 0.25em 0.25em 0.75em rgba(250, 0, 0, .25),
    0.125em 0.125 0.25em rgba(100, 100, 0, .1 5);

    --blue : #3c31dd;
    --red:   rgb(255, 70, 0)/*#dd3f31*/;
    --dark: #0f0f10;
    --pink: #ff0066;
}

@media (min-width: 800px){
    :root{
        --fs-h1: 4.5rem;
        --fs-h2: 3.75rem;
        --fs-h3: 1.5rem;
        --fs-body: 1.125rem;
    }
}

/* General Styles */

body{
    background-color: var(--dark);
    color: var(--clr-light);
    margin: 0;
    font-family: var(--ff-primary);
    font-size: var(--fs-body);
}
section{
    padding: 5em 2em;
}

img{
    display: block;
    max-width: 100%;
}

strong{ font-weight: var(--fw-bold);}

:focus{
    outline: 3px solid var(--red);
    outline-offset: 3px;
}


/* Typography */

h1,
h2,
h3{
    line-height: 1;
    margin: 0;
}

h1{font-size: var(--fs-h1);}
h2{font-size: var(--fs-h2);}
h3{font-size: var(--fs-h3);}

.section--title{
    margin-bottom: .25em;
}

.section--title--intro{
    font-weight: var(--fw-reg);
}
.section--title--intro strong{
    display: block;
}
.section--subtitle{
    margin: 0;
    font-size: var(--fs-h3);

}

.section--subtitle--intro,
.section--subtitle--about{
    background: var(--red);
    
    background-image: url("./assets/img/poly1.svg");
    border-radius: 1em;
    padding: .25em 1em;
    font-family: var(--ff-secondary);
    margin-bottom: 1em;
}


/* Intro Section */

.intro{
    position: relative;
    overflow: hidden;
}

.intro__img{
    box-shadow: var(--bs);
    width: 40vw;
}
.intro__img:hover{
    transform: scale(1.1);
    transition: .5s;
}
@media (max-width:800px){

    .intro{
        min-height: 620px;
        overflow: hidden;
    }

    
   
}


@media (min-width: 600px){

    .intro{
        display: grid;
        width: min-content;
        margin: 0 auto;
        grid-column-gap: 1em;
        grid-template-areas:
            "img title"
            "img subtitle";
        grid-template-columns: min-content max-content;
    }
    .intro__img{
        grid-area: img;
        min-width: 350px;
        position: relative;
        z-index: 2;
        border-radius: 0.5em;
    }

    .section--subtitle--intro{
        align-self: start;
        grid-column: -1 / 1;
        grid-row: 2;
        text-align: right;
        position: relative;
        left: -1.5em;/*cancel-left-overflow : 0em*/
        width: 100%;
    }
    .section--title--intro{
        width: 460px;
        height: 380px;
    }
   
}



/* header */
header{
    display: flex;
    justify-content: space-between;
    padding: 1em;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    overflow: hidden;
}
@media screen and (min-width: 1200px) {
    .bars{
        display: none;
    }
}
  

.waveUp{
    position: absolute;
    width: 100vw;
    top: 0;
    left: 0;
    z-index: 0;
}

.logo{
    max-width: 10vw;
    max-height: 8vw;
    position: absolute;
    overflow: hidden;
}
.logo > img{
    background-position: center;
    background-size: cover;
    position: relative;
    transform: scaleY(2);
}

.nav{
    position: fixed;
    background: var(--red);
    color: var(--dark);
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 100;
    display: none;

    transition: transform 250ms cubic-bezier(.5, 0, .5, 1);
}

.show{
    display: block;
}

.nav--list{
    list-style: none;
    display: flex;
    height: 100%;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    margin: 0;
    padding: 0;
}

.nav--link{
    color: inherit;
    font-weight : var(--fw-bold);
    font-size : var(--fs-h2);
    text-decoration: none;
}

.nav--item:hover{
    background-color: var(--glass-bg);
    padding: 10px 25vw;
    transform: scale(1.2);
    color: var(--clr-light);
    font-weight: bold;
    transition: .3s;
    border-radius: 25em;
    box-shadow: 0 8px 32px 0 
    rgba(0, 0, 0, 1);
    backdrop-filter: blur(6px);
    border: 1px solid rgba(255, 255, 255, 0.18);
    
}
.nav--item{
    
    border-bottom: 5px solid var(--dark);
}



/* Button styles */

.btn{
    border: 2px solid var(--red);
    font-weight: var(--fw-bold);
    font-size: var(--fs-h3);
    padding: 20px 20px;
    border-radius: 20em;
    background-color: var(--clr-dark);
    margin-top: 20px;
}
.btn a{
    color: var(--clr-light);
    position: relative;
    
    
    text-decoration: none;
}
.btn a:hover{
    color: var(--dark);
    
}

.btn:hover{
    cursor: pointer;
    transform: scalex(1.1);
    transition: 0.4s;
    
    border: 4px solid var(--dark);
    background-color: var(--red);
}


/* My services*/

.my-services{
    background-color: var(--clr-dark);
    background-image: url("../src/assets/img/friendly-dev.jpg");
    background-size: cover;
    background-blend-mode: multiply ;
    color: var(--clr-light);
    text-align: center;
    overflow: hidden;
}

.section--title--services{
    color: var(--red);
    position: relative;
}
.section--title--services::after{
    content: '';
    display: block;
    width: 25%;
    height: 1px;
    margin: 0.5em auto 1em;
    background: currentColor;
    opacity: 1.5;
}

.service{
    max-width: 500px;
    margin: 0 auto ;
}



@media (min-width:800px){
    .services{
        display: flex;
        max-width: 1000px;
        margin-left: auto;
        margin-right: auto;
    }

    .service + .service{
        margin-left: 2em;

    }
}

.services{
    margin-bottom: 3em;
}


/* About Me Section: */
.about-me{
    position: relative;
    max-width: 1000px;
    display: block;

    margin: 0 auto;
    overflow: hidden;
}
.section--subtitle--about{
    height: 70px;
}
@media screen and (max-width: 768px) {
    .bars{
        display: block;
    }
    .intro{
        min-height: 700px;
        max-height: 700px;
    }
    .section--subtitle--about{
        height: 70px;
    }
    .section--title--intro{
        min-height: 250px;
    }
    .about-me{
        overflow: hidden;
        height: 1300px;
        max-height: 1300px;
    }
    .about-typed-paragraph{
        min-height: 80px;
    }
}

.about-typed-paragraph{
    height: 70px;
    color: var(--red);
}

.about-me--img{
    box-shadow: var(--bs);
    filter: brightness(90%);
}
@media only screen and (max-width: 770px){

    .bars i{
        font-size: 10px;
    }
}

@media (min-width: 600px){
    .about-me{
        display: grid;
        grid-template-columns: 1fr 300px;
        grid-template-areas: 
            "title img"
            "subtitle img"
            "text img";
        grid-column-gap: 2em ;
    }

    
    .section--title--about{
        grid-area: title;
    }

    .section--subtitle--about{
        grid-column: 1 / -1;
        grid-row: 2;
        position: relative;
        left:  -2em;
        width: calc(100% + 4em);
        padding-left: 1em;
        padding-right: calc(300px + 4em);
        background-image: url("./assets/img/poly1.svg");
        
    }


    .about-me--img{
        grid-area: img;
        position: relative;
        z-index: 2;
    }
}

/* End About Section */

/* Start My Work Section */

.my-work{
    background-color: var(--clr-dark);
    color: var(--clr-light);
    text-align: center;
    overflow: hidden;
}

.section--subtitle--work{
    color: var(--red);
    font-weight: var(--fw-bold);
    margin-bottom: 2em;
}
.portfolio{
    display:flex;
    justify-content: space-evenly;
}

.portfolio--item{
    max-width: 30vw;
    background: var(--red);
    overflow: hidden;
}

.portfolio--img{
    transition:
        transform 750ms cubic-bezier(.5, 0, .5, 1),
        opacity 250ms linear;
}

.portfolio--item:focus{
    position: relative;
    z-index: 2;
}

.portfolio--img:hover,
.portfolio--item:focus .portfolio--img{
    transform: scale(1.2);
    opacity: .8;
}

/* End Of Work Section */

/* Start of Footer Section */

.footer{
    background-image: url('../src/assets/img/wave-downs3.svg');
    background-size: cover;
    background-position-y: center;
    color: var(--clr-light);
    text-align: center;
    padding: 2.5em 0;
    font-size: var(--fs-h3);
    background-repeat: no-repeat;
    border-radius: 1em;
}

.footer a {color: inherit; text-decoration: none;}

.footer-link{
    font-weight: var(--fw-bold);
}

.footer-link:hover,
.social-list--link:hover{
    opacity: .7;
    transform: scale(1.25);
}

.footer-link:hover{
    text-decoration: underline;
}


.social-list{
    list-style: none;
    display: flex;
    justify-content: center;
    margin: 2em 0;
    padding: 0;
}

.social-list--item{
    margin: 0 .5em;
}

.social-list--link{
    padding: .5em;
}

.sign{
    margin-top: 20px;
    position: relative;
    bottom: 0;
    font-style: italic;
    font-size: 25px;
    font-weight: bold;
    color: var(--clr-light);
}

/* End of Footer Section */


.bars{
    z-index: 101;
    position: absolute;
    right: 2em;
    top: 1em;
}

.bars i{
    font-size: 50px;
    position: relative;
    display: block;

    color: antiquewhite;
    z-index: 101;
    transition: 0.4ms;
    box-shadow: 0 2px 4px rgba(100, 100, 100, 0.1),
        0 4px 8px rgba(100, 100, 100, 0.1),
        0 8px 16px rgba(100, 100, 100, 0.1),
        0 16px 32px rgba(100, 100, 100, 0.1),
        0 32px 64px rgba(100, 100, 100, 0.1);
}

.lang--div{
    position: fixed;
    z-index: 101;
    bottom: 3em;
    background-color: var(--red);
    right: 0px;
    padding-left: 10px;
    border-top-left-radius: 2em;
    border-bottom-left-radius: 2em;
    opacity: 0.7;
}

.lang-toggle{
    font-size: 30px;
    position: relative;
    color: var(--clr-light);
    font-weight: bold;
    border-radius: 2em;
    z-index: 101;
    margin-right: 1em;
}

.lang--div:hover{
    opacity: 1;
    cursor: pointer;
    transform: scale(1.3);
    transition: 0.4s ease-in-out;
}

.skills{
    position: relative;
    display: flex;
    flex-direction: row;
    text-align: center;
    margin-bottom: 2em;
    justify-content: center;
}

.skills--list{
    list-style: none;
    display: flex;
    margin: 0 .2em;
    text-align: center;

}
.skills--item{
    position: relative;
    font-size: 5vw;
    color: gold;
    margin: 0 .5em;
}


.skills--item span {
    display: none;
    position: absolute;
    top: 20px;
    left: 0;
    font-size: 14px;
    background-color: var(--clr-dark);
    padding: 5px;
    color: #fff;
    border-radius: 5px;
}
.skills--item:hover span{
    display: block;
}